.elements {
  max-width: 880px;
  display: flex;
  flex-wrap: wrap;
  margin: 46.25px auto 0;
  row-gap: 20px;
  column-gap: 17px;
}

@media screen and (max-width: 890px) {
  .elements {
    max-width: 581px;
  }
}

@media screen and (max-width: 590px) {
  .elements {
    max-width: 282px;
  }
}

